'use client'

import { PublicKey } from '@solana/web3.js'
import { useMemo, useState, useEffect } from 'react'
import { useCoinFlipProgram, useCoinFlipProgramAccount } from './coin-flip-data-access'
import { Button } from '@/components/ui/button'
import { useWallet } from '@solana/wallet-adapter-react'
import { Coins, Trophy, Target } from 'lucide-react'
import BN from 'bn.js'

const LAMPORTS_PER_SOL = 1_000_000_000

function GameSelector() {
  const { accounts } = useCoinFlipProgram()
  const [selectedGame, setSelectedGame] = useState<PublicKey | null>(null)

  const availableGames = useMemo(() => {
    return accounts.data?.filter((account: any) => 
      account.account.totalAmount.toNumber() > 0
    ) || []
  }, [accounts.data])

  useEffect(() => {
    if (availableGames.length > 0 && !selectedGame) {
      setSelectedGame(availableGames[0].publicKey)
    }
  }, [availableGames, selectedGame])

  if (availableGames.length === 0) {
    return (
      <div className="text-center py-12 bg-gray-50 rounded-xl">
        <Coins className="h-16 w-16 text-gray-400 mx-auto mb-4" />
        <h3 className="text-xl font-semibold text-gray-700 mb-2">No Games Available</h3>
        <p className="text-gray-500">No active games found. Contact an admin to start a game.</p>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {availableGames.length > 1 && (
        <div className="bg-white rounded-xl p-6 shadow-sm border">
          <h3 className="text-lg font-semibold mb-4">Select a Game</h3>
          <div className="grid gap-3">
            {availableGames.map((game: any) => (
              <button
                key={game.publicKey.toString()}
                onClick={() => setSelectedGame(game.publicKey)}
                className={`p-4 rounded-lg border-2 transition-all text-left ${
                  selectedGame?.equals(game.publicKey)
                    ? 'border-blue-500 bg-blue-50'
                    : 'border-gray-200 hover:border-gray-300'
                }`}
              >
                <div className="flex justify-between items-center">
                  <div>
                    <p className="font-medium">Game #{game.account.totalGames.toString()}</p>
                    <p className="text-sm text-gray-600">
                      Vault: {(game.account.totalAmount.toNumber() / LAMPORTS_PER_SOL).toFixed(4)} SOL
                    </p>
                  </div>
                  <div className="text-sm text-gray-500">
                    Fee: {game.account.feePercentage.toString()}%
                  </div>
                </div>
              </button>
            ))}
          </div>
        </div>
      )}

      {selectedGame && (
        <BettingInterface gameStatePubkey={selectedGame} />
      )}
    </div>
  )
}

function BettingInterface({ gameStatePubkey }: { gameStatePubkey: PublicKey }) {
  const wallet = useWallet()
  const { accounts, program } = useCoinFlipProgram()
  
  const [betChoice, setBetChoice] = useState<number>(0)
  const [placeBetAmount, setPlaceBetAmount] = useState<number>(0)
  const [betResult, setBetResult] = useState<null | 'won' | 'lost' | 'pending'>(null)

  const gameStateData = useMemo(() => {
    return accounts.data?.find((account: any) => account.publicKey.equals(gameStatePubkey))
  }, [accounts.data, gameStatePubkey])

  const betAccountQuery = useCoinFlipProgramAccount({
    user: wallet.publicKey || new PublicKey('11111111111111111111111111111111'),
    owner: gameStateData?.account?.owner || new PublicKey('11111111111111111111111111111111'),
    gameNumber: gameStateData?.account?.totalGames?.toNumber()
  })

  const potentialWinnings = useMemo(() => {
    if (!placeBetAmount || !gameStateData?.account) return 0
    const payout = placeBetAmount * 2
    const fee = (payout * gameStateData.account.feePercentage.toNumber()) / 100
    return payout - fee
  }, [placeBetAmount, gameStateData?.account])

  const existingBetInfo = useMemo(() => {
    const betData = betAccountQuery.accountQuery.data
    if (!betData?.settled) return null
    
    return {
      amount: betData.amount.toNumber() / LAMPORTS_PER_SOL,
      choice: betData.choice === 0 ? 'Heads' : 'Tails',
      won: betData.won
    }
  }, [betAccountQuery.accountQuery.data])

  useEffect(() => {
    if (betAccountQuery.placeBet.isPending) {
      setBetResult('pending')
    }
  }, [betAccountQuery.placeBet.isPending])

  const checkBetResult = async (gameNumber: number) => {
    if (!wallet.publicKey) return

    const gameNumberBuf = new BN(gameNumber).toArrayLike(Buffer, 'le', 8)
    const [correctBetAccount] = PublicKey.findProgramAddressSync(
      [Buffer.from('bet_account'), wallet.publicKey.toBuffer(), gameNumberBuf],
      (program as any).programId
    )

    let attempts = 0
    const maxAttempts = 10

    const attemptFetch = async () => {
      attempts++
      
      try {
        const betData = await (program.account as any).betAccount.fetch(correctBetAccount)
        
        if (betData?.settled) {
          setBetResult(betData.won ? 'won' : 'lost')
          setPlaceBetAmount(0)
          accounts.refetch()
          return
        }
        
        if (attempts < maxAttempts) {
          setTimeout(attemptFetch, 2000)
        } else {
          setBetResult(null)
          setPlaceBetAmount(0)
        }
      } catch (error) {
        if (attempts < maxAttempts) {
          setTimeout(attemptFetch, 2000)
        } else {
          setBetResult(null)
          setPlaceBetAmount(0)
        }
      }
    }

    setTimeout(attemptFetch, 3000)
  }

  const handlePlaceBet = async () => {
    if (!wallet.publicKey || !gameStateData?.account?.owner || !placeBetAmount || placeBetAmount <= 0) {
      return
    }

    setBetResult('pending')
    const amountLamports = placeBetAmount * LAMPORTS_PER_SOL

    try {
      const gameNumberForBet = betAccountQuery.currentGameNumber
      
      await betAccountQuery.placeBet.mutateAsync({
        amount: amountLamports,
        choice: betChoice
      })
      
      checkBetResult(gameNumberForBet)
    } catch (error) {
      console.error("Bet failed:", error)
      setBetResult(null)
    }
  }

  if (!gameStateData?.account) {
    return <div className="text-center py-8">Loading game...</div>
  }

  return (
    <div className="space-y-8">
      {/* Game Info Header */}
      <div className="text-center bg-gradient-to-r from-blue-50 to-purple-50 rounded-xl p-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">ðŸª™ Coin Flip Game</h1>
        <p className="text-gray-600 mb-6">Double your SOL or lose it all!</p>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 max-w-md mx-auto">
          <div className="bg-white rounded-lg p-4 shadow-sm">
            <div className="text-2xl font-bold text-blue-600">
              {gameStateData.account.totalGames.toString()}
            </div>
            <div className="text-sm text-gray-600">Games Played</div>
          </div>
          
          <div className="bg-white rounded-lg p-4 shadow-sm">
            <div className="text-2xl font-bold text-green-600">
              {(gameStateData.account.totalAmount.toNumber() / LAMPORTS_PER_SOL).toFixed(2)}
            </div>
            <div className="text-sm text-gray-600">SOL in Vault</div>
          </div>
          
          <div className="bg-white rounded-lg p-4 shadow-sm">
            <div className="text-2xl font-bold text-purple-600">
              {gameStateData.account.feePercentage.toString()}%
            </div>
            <div className="text-sm text-gray-600">House Fee</div>
          </div>
        </div>
      </div>

      {/* Previous Bet Result */}
      {existingBetInfo && (
        <div className={`text-center p-6 rounded-xl ${
          existingBetInfo.won 
            ? 'bg-green-50 border-2 border-green-200' 
            : 'bg-red-50 border-2 border-red-200'
        }`}>
          <div className="text-4xl mb-2">
            {existingBetInfo.won ? 'ðŸŽ‰' : 'ðŸ˜¢'}
          </div>
          <h3 className="text-xl font-bold mb-2">
            {existingBetInfo.won ? 'You Won!' : 'You Lost!'}
          </h3>
          <p className="text-gray-700">
            You bet {existingBetInfo.amount.toFixed(4)} SOL on {existingBetInfo.choice}
          </p>
        </div>
      )}

      {/* Betting Interface */}
      <div className="max-w-md mx-auto space-y-6">
        {/* Bet Amount */}
        <div className="bg-white rounded-xl p-6 shadow-sm border">
          <label className="block text-sm font-medium text-gray-700 mb-3">
            Bet Amount
          </label>
          <div className="flex items-center gap-3">
            <input
              type="number"
              min={0}
              step={0.1}
              placeholder="0.0"
              value={placeBetAmount || ''}
              onChange={(e) => setPlaceBetAmount(Number(e.target.value))}
              disabled={betAccountQuery.placeBet.isPending}
              className="flex-1 px-4 py-3 border rounded-lg text-lg font-semibold text-center"
            />
            <span className="text-lg font-medium text-gray-600">SOL</span>
          </div>
          
          {/* Quick Amount Buttons */}
          <div className="flex gap-2 mt-3">
            {[0.1, 0.5, 1.0].map(amount => (
              <button
                key={amount}
                onClick={() => setPlaceBetAmount(amount)}
                disabled={betAccountQuery.placeBet.isPending}
                className="flex-1 py-2 text-sm bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors"
              >
                {amount} SOL
              </button>
            ))}
          </div>
        </div>

        {/* Potential Winnings */}
        {placeBetAmount > 0 && (
          <div className="bg-yellow-50 border-2 border-yellow-200 rounded-xl p-4 text-center">
            <p className="text-lg font-semibold text-yellow-800">
              Potential Winnings: {potentialWinnings.toFixed(4)} SOL
            </p>
            <p className="text-sm text-yellow-700">
              2x your bet minus {gameStateData.account.feePercentage.toString()}% house fee
            </p>
          </div>
        )}

        {/* Choice Selection */}
        <div className="space-y-3">
          <label className="block text-sm font-medium text-gray-700 text-center">
            Choose Your Side
          </label>
          <div className="grid grid-cols-2 gap-4">
            <button
              onClick={() => setBetChoice(0)}
              disabled={betAccountQuery.placeBet.isPending}
              className={`p-6 rounded-xl border-2 transition-all ${
                betChoice === 0
                  ? 'border-orange-500 bg-orange-50 text-orange-700'
                  : 'border-gray-200 hover:border-gray-300 text-gray-700'
              }`}
            >
              <div className="text-4xl mb-2">ðŸª™</div>
              <div className="font-semibold">Heads</div>
            </button>
            
            <button
              onClick={() => setBetChoice(1)}
              disabled={betAccountQuery.placeBet.isPending}
              className={`p-6 rounded-xl border-2 transition-all ${
                betChoice === 1
                  ? 'border-blue-500 bg-blue-50 text-blue-700'
                  : 'border-gray-200 hover:border-gray-300 text-gray-700'
              }`}
            >
              <div className="text-4xl mb-2">ðŸŽ¯</div>
              <div className="font-semibold">Tails</div>
            </button>
          </div>
        </div>

        {/* Place Bet Button */}
        <Button
          onClick={handlePlaceBet}
          disabled={betAccountQuery.placeBet.isPending || !placeBetAmount || placeBetAmount <= 0 || !wallet.publicKey}
          className="w-full py-4 text-lg font-bold bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600"
        >
          {betAccountQuery.placeBet.isPending ? (
            <span className="flex items-center gap-2">
              <div className="animate-spin h-5 w-5 border-2 border-white border-t-transparent rounded-full"></div>
              Flipping Coin...
            </span>
          ) : (
            'ðŸŽ² Flip the Coin!'
          )}
        </Button>

        {/* Bet Status */}
        {betResult === 'won' && (
          <div className="text-center p-4 bg-green-50 rounded-xl border-2 border-green-200">
            <div className="text-3xl mb-2">ðŸŽ‰</div>
            <p className="text-green-600 font-bold text-lg">You Won!</p>
          </div>
        )}
        {betResult === 'lost' && (
          <div className="text-center p-4 bg-red-50 rounded-xl border-2 border-red-200">
            <div className="text-3xl mb-2">ðŸ˜¢</div>
            <p className="text-red-600 font-bold text-lg">You Lost!</p>
          </div>
        )}
        {betResult === 'pending' && (
          <div className="text-center p-4 bg-blue-50 rounded-xl border-2 border-blue-200">
            <div className="text-3xl mb-2">â³</div>
            <p className="text-blue-600 font-semibold">Processing your bet...</p>
          </div>
        )}
      </div>
    </div>
  )
}

export default function CoinFlipGame() {
  const wallet = useWallet()

  if (!wallet.publicKey) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-purple-50 flex items-center justify-center">
        <div className="text-center">
          <Coins className="h-20 w-20 text-gray-400 mx-auto mb-6" />
          <h1 className="text-3xl font-bold text-gray-900 mb-4">Coin Flip Game</h1>
          <p className="text-gray-600 mb-6">Connect your wallet to start playing!</p>
        </div>
      </div>
    )
  }

  return (
  <div className="bg-gradient-to-br from-blue-50 to-purple-50 py-8">
      <div className="max-w-4xl mx-auto px-4">
        <GameSelector />
      </div>
    </div>
  )
}